// 数据统计相关接口
import request from '@/utils/request'

export function getLineChartData(params) {
  return request({
    url: `/data_analyze/fd_ask`,
    method: 'get',
    params: params
  })
}
export function getOrderChartData(params) {
  return request({
    url: `/data_analyze/orders`,
    method: 'get',
    params: params
  })
}
