<template>
  <div :class="className" :style="{ height: height, width: width }" />
</template>

<script>
import echarts from 'echarts' // echarts theme
import _ from 'lodash'
require('echarts/theme/macarons')

export default {
  name: 'countLine',
  props: {
    title: {
      type: Object,
      default: () => {}
    },
    className: {
      type: String,
      default: 'lineChart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '350px'
    },
    autoResize: {
      type: Boolean,
      default: true
    },
    chartData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      lineChart: null,
      sidebarElm: null,
      colorArr: ['#F56C6C', '#67C23A', '#E6A23C', '#409EFF'],
      lineNameArr: ['服务中的合约总数', '咨询总数', '待接入总数', '转诊中总数'],
      seriesArr: [],
      legendData: []
    }
  },
  watch: {
    chartData: {
      deep: true,
      handler(val) {
        if (this.lineChart) {
          this.lineChart.dispose()
          this.lineChart = null
        }
        this.initChart()
      }
    }
  },
  mounted() {
    // this.initChart()
    if (this.autoResize) {
      this.__resizeHandler = _.debounce(() => {
        if (this.lineChart) {
          this.lineChart.resize()
        }
      }, 100)
      window.addEventListener('resize', this.__resizeHandler)
    }
  },
  beforeDestroy() {
    if (!this.lineChart) {
      return
    }
    if (this.autoResize) {
      window.removeEventListener('resize', this.__resizeHandler)
    }

    this.sidebarElm &&
      this.sidebarElm.removeEventListener(
        'transitionend',
        this.sidebarResizeHandler
      )

    this.lineChart.dispose()
    this.lineChart = null
  },
  methods: {
    sidebarResizeHandler(e) {
      if (e.propertyName === 'width') {
        this.__resizeHandler()
      }
    },
    getDataNameByIndex(ind) {
      switch (ind) {
        case 0:
          return 'totalContract'
        case 1:
          return 'consult'
        case 2:
          return 'waitting'
        case 3:
          return 'transfer'
        default:
          return ''
      }
    },
    setSeriesArr(params) {
      this.seriesArr = []
      this.lineNameArr.forEach((item, index) => {
        this.seriesArr.push({
          name: item,
          smooth: true,
          type: 'line',
          lineStyle: {
            opacity: 1,
            width: 3
          },
          itemStyle: {
            color: this.colorArr[index]
          },
          data: params[this.getDataNameByIndex(index)],
          animationDuration: 2800,
          animationEasing: 'cubicInOut'
        })
        this.setLegendData(index)
      })
    },
    setLegendData(ind) {
      // return
      this.legendData.push({
        name: this.lineNameArr[ind],
        // 强制设置图形为圆。
        icon: 'circle',
        // 设置文本为红色
        textStyle: {
          color: this.colorArr[ind]
        }
      })
    },
    setOptions(params) {
      // console.log(params.xAxisData)
      this.setSeriesArr(params)
      this.lineChart.setOption(
        {
          title: {
            text: this.title.text,
            subtext: this.title.subtext,
            lineHeight: 100,
            textStyle: {
              fontStyle: 'normal',
              color: '#000'
            }
          },
          toolbox: {
            show: true,
            left: 570,
            top: 0,
            feature: {
              // restore: { show: true },
              // magicType: { type: ['line', 'bar'] },
              saveAsImage: { show: true }
            }
          },
          dataZoom: [
            {
              id: 'dataZoomX',
              type: 'inside',
              xAxisIndex: [0],
              filterMode: 'filter'
            }
            // {
            //   id: 'dataZoomY',
            //   type: 'inside',
            //   yAxisIndex: [0],
            //   filterMode: 'empty'
            // }
          ],
          xAxis: {
            data: params.xAxisData,
            boundaryGap: false,
            axisLabel: {
              rotate: '60',
              interval: this.chartData.totalContract.length > 30 ? 20 : 0
            },
            axisLine: {
              onZero: false,
              lineStyle: {
                color: this.colorArr
              }
            },
            axisTick: {
              show: true
            }
          },
          grid: {
            left: 10,
            right: 10,
            bottom: 20,
            top: 50,
            containLabel: true
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross'
            },
            padding: [5, 10]
          },
          yAxis: {
            axisTick: {
              show: true
            }
          },
          legend: {
            top: 4,
            left: 150,
            data: this.legendData
          },
          series: this.seriesArr
        },
        true
      )
    },
    initChart() {
      this.lineChart = echarts.init(this.$el, this.className)
      if (this.chartData) {
        this.setOptions(this.chartData)
      }
    }
  }
}
</script>
