<template>
  <div :class="className" :style="{ height: height, width: width }" />
</template>

<script>
import echarts from 'echarts' // echarts theme
import _ from 'lodash'
require('echarts/theme/macarons')

export default {
  name: 'pieChart',
  props: {
    className: {
      type: String,
      default: 'pieChart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '350px'
    },
    autoResize: {
      type: Boolean,
      default: true
    },
    title: {
      type: Object,
      default: () => {}
    },
    seriesData: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      pieChart: null,
      sidebarElm: null,
      colorArr: ['#36A5FB', '#FF005A', '#EF2136', '#7FF468'],
      nameArr: ['全部', '健康管理师', '营养师', '医生'],
      seriesArr: []
    }
  },
  watch: {
    seriesData() {
      console.log('sss')
      this.initChart()
    }
  },
  mounted() {
    this.initChart()
    if (this.autoResize) {
      this.__resizeHandler = _.debounce(() => {
        if (this.pieChart) {
          this.pieChart.resize()
        }
      }, 100)
      window.addEventListener('resize', this.__resizeHandler)
    }
  },
  beforeDestroy() {
    if (!this.pieChart) {
      return
    }
    if (this.autoResize) {
      window.removeEventListener('resize', this.__resizeHandler)
    }
    this.sidebarElm &&
      this.sidebarElm.removeEventListener(
        'transitionend',
        this.sidebarResizeHandler
      )
    this.pieChart.dispose()
    this.pieChart = null
  },
  methods: {
    sidebarResizeHandler(e) {
      if (e.propertyName === 'width') {
        this.__resizeHandler()
      }
    },
    setSeriesArr() {
      this.nameArr.forEach((item, index) => {
        this.seriesArr.push({
          name: item,
          value: this.seriesData[index]
        })
      })
    },
    setOptions(params) {
      this.setSeriesArr()
      console.log('seriesArr==', this.seriesArr)
      this.pieChart.setOption({
        title: {
          text: this.title.text,
          subtext: this.title.subtext,
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 'left',
          data: this.nameArr
        },
        series: [
          {
            name: '访问来源',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: this.seriesArr,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
    },
    initChart() {
      this.pieChart = echarts.init(this.$el, this.className)
      this.setOptions()
    }
  }
}
</script>
