<template>
  <div class="chart_wrap margin-top-20">
    <div class="order_wrap margin-bottom-30">
      <el-row class="time_wrap">
        <el-col :span="5" class="align-left">
          <el-button size="large" type="text" style="line-height:2px;"
            >请选择日期范围：</el-button
          >
        </el-col>
        <el-col :span="13">
          <el-date-picker
            size="mini"
            v-model="datetimeRange"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          >
          </el-date-picker>
        </el-col>
        <el-button
          class="margin-left-20"
          size="mini"
          type="primary"
          @click="getDataByTime('order')"
          >搜索</el-button
        >
        <el-button
          class="margin-left-20"
          size="mini"
          type="primary"
          @click="resetDataByTime('order')"
          >重置</el-button
        >
      </el-row>
      <BarChart :title="barTitle" :barChartData="barChartData"></BarChart>
    </div>
    <div class="chat_wrap margin-top-30">
      <el-row class="time_wrap">
        <el-col :span="5" class="align-left">
          <el-button size="large" type="text" style="line-height:2px;"
            >请选择时间范围：</el-button
          >
        </el-col>
        <el-col :span="13">
          <el-date-picker
            size="mini"
            v-model="datetimeRange2"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期时间"
            end-placeholder="结束日期时间"
          >
          </el-date-picker>
        </el-col>
        <el-button
          class="margin-left-20"
          size="mini"
          type="primary"
          @click="getDataByTime()"
          >搜索</el-button
        >
        <el-button
          class="margin-left-20"
          size="mini"
          type="primary"
          @click="resetDataByTime()"
          >重置</el-button
        >
      </el-row>
      <LineChart
        class="margin-top-30"
        :title="lineTitle"
        :chartData="lineChartData"
      ></LineChart>
    </div>
    <PieChart
      v-if="false"
      :title="pieTitle"
      :seriesData="pieSeriesData"
    ></PieChart>
  </div>
</template>
<script>
import BarChart from '@/views/charts/components/BarChart'

import PieChart from '@/views/charts/components/PieChart'
import LineChart from '@/views/charts/components/LineChart'
import { getLineChartData, getOrderChartData } from '@/api/charts'
export default {
  name: 'count',
  components: { LineChart, PieChart, BarChart },
  data() {
    return {
      datetimeRange: [],
      datetimeRange2: [],
      start: '',
      end: '',
      lineChartData: {},
      barChartData: {},
      pieSeriesData: [200, 22, 55, 66],
      lineTitle: {
        text: '咨询数据统计',
        subText: ''
      },
      barTitle: {
        text: '订单统计',
        subText: ''
      },
      pieTitle: {
        text: '咨询数据统计-饼图',
        subText: ''
      }
    }
  },
  methods: {
    resetDataByTime(type) {
      const params = {
        start: '',
        end: ''
      }
      if (type === 'order') {
        this.datetimeRange = []
        this.getOrderData(params)
      } else {
        this.datetimeRange2 = []
        this.getFdAskData(params)
      }
    },
    getDataByTime(type) {
      let range = type === 'order' ? this.datetimeRange : this.datetimeRange2
      if (range && range.length >= 2) {
        this.start = range[0]
        this.end = range[1]
      }
      const params = {
        start: this.start,
        end: this.end
      }
      if (type === 'order') {
        this.getOrderData(params)
      } else {
        this.getFdAskData(params)
      }
    },
    getFdAskData(params) {
      getLineChartData(params).then(res => {
        const data = res.data.list
        console.log('getLineChartData==', data)
        let obj = {
          totalContract: [],
          consult: [],
          waitting: [],
          transfer: [],
          xAxisData: []
        }
        data.forEach((item, index) => {
          obj.totalContract.push(item.ask_record.in_service_contracts)
          obj.consult.push(item.ask_record.asking_num)
          obj.waitting.push(item.ask_record.pending_num)
          obj.transfer.push(item.ask_record.transfer_num)
          obj.xAxisData.push(this.formatTime(item.created_at, 'MDHM'))
        })
        console.log('obj===', obj)
        this.lineChartData = obj
      })
    },
    getOrderData(params) {
      getOrderChartData(params).then(res => {
        console.log('barChartData==', res)
        const data = res.data.list
        let obj = {
          singleAsk: [],
          fd: [],
          total: [],
          xAxisData: []
        }
        data.forEach((item, index) => {
          obj.total.push(item.total)
          obj.fd.push(item.FdServiceType ? item.FdServiceType : 0)
          obj.singleAsk.push(
            item.InquiryServiceType ? item.InquiryServiceType : 0
          )
          obj.xAxisData.push(item.day)
        })
        this.barChartData = obj
      })
    }
  },
  created() {
    this.getFdAskData()
    this.getOrderData()
  }
}
</script>
<style scope style="scss">
.chart_wrap {
  overflow-x: auto;
  min-width: 1480px;
}
.time_wrap {
  float: right;
  min-width: 640px;
  z-index: 199;
}
</style>
