<template>
  <div :class="className" :style="{ height: height, width: width }" />
</template>

<script>
import echarts from 'echarts' // echarts theme
import _ from 'lodash'
require('echarts/theme/macarons')

export default {
  name: 'barChart',
  props: {
    title: {
      type: Object,
      default: () => {}
    },
    className: {
      type: String,
      default: 'barChart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '350px'
    },
    autoResize: {
      type: Boolean,
      default: true
    },
    barChartData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      barChart: null,
      sidebarElm: null,
      colorArr: ['orange', '#67C23A', '#409EFF'],
      lineNameArr: ['家庭医生订单总数', '单次问诊订单总数', '订单总量'],
      seriesArr: [],
      legendData: []
    }
  },
  watch: {
    barChartData: {
      deep: true,
      handler(val) {
        if (this.barChart) {
          this.barChart.dispose()
          this.barChart = null
        }
        this.initChart()
      }
    }
  },
  mounted() {
    this.initChart()
    if (this.autoResize) {
      this.__resizeHandler = _.debounce(() => {
        if (this.barChart) {
          this.barChart.resize()
        }
      }, 100)
      window.addEventListener('resize', this.__resizeHandler)
    }
  },
  beforeDestroy() {
    if (!this.barChart) {
      return
    }
    if (this.autoResize) {
      window.removeEventListener('resize', this.__resizeHandler)
    }

    this.sidebarElm &&
      this.sidebarElm.removeEventListener(
        'transitionend',
        this.sidebarResizeHandler
      )

    this.barChart.dispose()
    this.barChart = null
  },
  methods: {
    sidebarResizeHandler(e) {
      if (e.propertyName === 'width') {
        this.__resizeHandler()
      }
    },
    getDataByxAxis(ind) {
      switch (ind) {
        case 0:
          return 'fd'
        case 1:
          return 'singleAsk'
        case 2:
          return 'total'
        default:
          return ''
      }
    },
    setSeriesArr() {
      this.seriesArr = []
      this.lineNameArr.forEach((item, index) => {
        if (index < this.lineNameArr.length - 1) {
          this.seriesArr.push({
            name: item,
            type: 'bar',
            stack: '总量',
            label: {
              show: true,
              position: 'insideRight'
            },
            barWidth: '25px',
            itemStyle: {
              color: this.colorArr[index]
            },
            data: this.barChartData[this.getDataByxAxis(index)],
            animationDelay: function(idx) {
              return idx * 10
            }
          })
        } else {
          this.seriesArr.push({
            name: item,
            smooth: true,
            type: 'line',
            lineStyle: {
              opacity: 1,
              width: 3
            },
            itemStyle: {
              color: this.colorArr[index]
            },
            label: {
              show: true,
              textStyle: {
                color: '#fff'
              }
            },
            data: this.barChartData[this.getDataByxAxis(index)]
          })
        }
        this.setLegendData(index)
      })
    },
    setLegendData(ind) {
      this.legendData.push({
        name: this.lineNameArr[ind],
        textStyle: {
          color: this.colorArr[ind]
        }
      })
    },
    setOptions(params) {
      this.barChart.setOption(
        {
          title: {
            text: this.title.text,
            subtext: this.title.subtext,
            lineHeight: 100,
            textStyle: {
              fontStyle: 'normal',
              color: '#000'
            }
          },
          toolbox: {
            left: 470,
            top: 0,
            feature: {
              // restore: { show: true },
              saveAsImage: { show: true }
            }
          },
          dataZoom: [
            {
              id: 'dataZoomX',
              type: 'inside',
              xAxisIndex: [0],
              filterMode: 'filter'
              // start: 0,
              // // zoomLock: true,
              // end: 40
            }
          ],
          grid: {
            left: 10,
            right: 10,
            bottom: 20,
            top: 50,
            containLabel: true
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'line' // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          xAxis: {
            type: 'category',
            data: params.xAxisData,
            axisTick: {
              show: true
            }
          },
          yAxis: {
            type: 'value'
          },
          legend: {
            top: 4,
            left: 100,
            data: this.legendData
          },
          series: this.seriesArr
        }
        // true //true重绘
      )
    },

    initChart() {
      this.barChart = echarts.init(this.$el, this.className)
      if (this.barChartData) {
        this.setSeriesArr()
        this.setOptions(this.barChartData)
      }
    }
  }
}
</script>
